.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
}

h1, h2, h3, h4 {
  text-align: center;
}

div {
  margin-bottom: 20px;
}

label {
  display: block;
  margin: 10px 0 5px;
}

select, input {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
}

button {
  padding: 10px 20px;
  margin: 0 10px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

button:hover:not(:disabled) {
  background-color: #0056b3;
}

pre {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 5px;
}

.connector-block {
  border: 1px solid #ccc;
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
}

.pin-assignment {
  border: 1px solid #ddd;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.pin-assignment h4 {
  margin: 0 0 10px;
  font-size: 16px;
}

.destination {
  display: flex;
  align-items: center;
  margin: 5px 0;
}

.destination span {
  flex: 1;
}

.destination button {
  padding: 5px 10px;
  background-color: #dc3545;
}

.destination button:hover {
  background-color: #c82333;
}

.preview {
  margin: 20px 0;
}